@import '../adaptive/adaptive-index';
@import '../adaptive/adaptive-exp';
@import '../adaptive/adaptive-blog';

.title {
    font-family: Verdana;
    font-size: 36px;
    font-weight: 700;
    text-align: center;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.3);
}

@media (max-width:1201px) {

    section,
    header,
    footer {
        padding: 0 20px;
    }

    .header {
        &__title {
            font-size: 32px;
            line-height: 30px;
            margin-left: 10px;

            span {
                font-size: 20px;
            }
        }

        &__search {
            input {
                width: 200px;
                height: 35px;
            }
        }
    }

    .nav {
        &__links {
            a {
                font-size: 22px;
            }
        }
    }

    .next,
    .prev {
        background: rgba(41, 105, 200, .75);

        transition: all .3s ease;

        &:hover {
            background: rgba(41, 105, 200, .50);
            transition: all .3s ease;

        }
    }

    .next {
        right: 0;
        z-index: 1000;
    }


    .prev {
        left: 0;
        z-index: 1000;
    }

    .footer {

        &__logo {
            width: 50%;

            img {
                display: block;
                margin: 0 auto;
            }
        }

        &__block {
            font-size: 16px;
            width: 50%;
            text-align: center !important;

            &:nth-child(1) {
                order: 1;

                img {
                    display: block;
                    margin: 0 auto;
                }
            }

            &:nth-child(2) {
                order: 3;


            }

            &:nth-child(3) {
                order: 2;

                img {
                    display: block;
                    margin: 0 auto;
                }
            }

            &:nth-child(4) {
                order: 4;
            }

        }
    }
}


@media (max-width:993px) {

    .header {
        &__title {
            font-size: 30px;
            line-height: 30px;
            margin-left: 10px;

            span {
                font-size: 20px;
            }
        }

        &__search {
            input {
                width: 200px;
                height: 35px;
            }
        }
    }

    .nav {
        &__links {
            a {
                font-size: 20px;
            }
        }
    }
}

@media (max-width:768px) {

    .upheader {
        &__wrapper {
            gap: 30px;
        }

        &__string {
            font-size: 12px;
        }

        &__socials {
            font-size: 20px;

            img {
                width: 25px;
            }
        }
    }

    .header {
        &__title {
            font-size: 25px;
            line-height: 25px;
            margin-left: 10px;

            span {
                font-size: 18px;
            }
        }

        &__wrapper {
            flex-wrap: wrap;
            justify-content: center;
        }

        &__search {
            margin: 20px 0;
            width: 75%;

            input {
                width: 100%;
                height: 35px;
            }
        }
    }

    .nav {
        &__links {
            a {
                font-size: 18px;
            }
        }
    }

    .footer {
        &__logo {
            width: 100px;
            margin: 0 auto;
        }

        &__block {
            img {
                width: 200px;
            }
        }
    }

    .downfooter {
        ul {
            font-size: 14px;
        }
    }
}

@media (max-width:568px) {

    section,
    header,
    footer {
        padding: 0 10px;
    }

    .upheader {
        &__wrapper {
            gap: 10px;
            flex-wrap: wrap;
            height: 100%;
        }

        &__string {
            font-size: 14px;
        }

        &__socials {
            font-size: 25px;

            img {
                width: 30px;
            }
        }
    }

    .header {
        &__title {
            font-size: 40px;
            line-height: 40px;
            text-align: center;
            margin-left: 10px;

            span {
                display: none;
            }
        }

        &__wrapper {
            flex-wrap: wrap;
            justify-content: center;

            div {
                width: 100%;
                margin: 0 auto;
            }
        }

        &__logo {
            img {
                display: block;
                margin: 0 auto;
            }
        }

        &__search {
            margin: 20px 0 !important;
            width: 75%;

            input {
                width: 100%;
                height: 35px;
            }
        }
    }

    .nav {
        &__links {
            gap: 20px;

            a {
                font-size: 16px;
                padding: 10px 0;
                height: 100%;
            }
        }
    }

    .footer {
        &__logo {
            width: 75px;
            margin: 0 auto;
        }

        &__block {
            font-size: 14px;

            img {
                width: 175px;
            }
        }
    }

    .downfooter {
        padding: 5px 0;

        ul {
            flex-wrap: wrap;
            font-size: 16px;
        }
    }
}