@import 'adaptive';
@import url('https://fonts.googleapis.com/css2?family=Special+Elite&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pangolin&family=Special+Elite&display=swap');


.template {
    margin-top: 50px;

    &__wrapper {
        position: relative;
        z-index: 10000000000000;
    }

    &__accordion {
        width: max-content;
        cursor: pointer;
        background: rgb(212, 212, 212);
        padding: 5px;
        border-radius: 5px;
    }

    &__text {
        font-size: 22px;
        gap: 15px;
        display: flex;
        align-items: center;
        justify-content: center;

        i {
            margin-top: 5px;
        }
    }

    &__select {
        display: flex;
        gap: 30px;
        max-height: 0;
        overflow: hidden;
        transition: all .3s ease;
        position: absolute;
        top: 100%;
        opacity: 0;
        background: rgba(0, 0, 0, .1);
        backdrop-filter: blur(10px);
    }
}

.button {
    width: 33%;
    padding: 10px;

    img {
        width: 100%;
        object-fit: cover;
    }
}

#editButton {
    width: 200px;
    background: rgb(65, 115, 223);
    height: 50px;
    border-radius: 5px;
    color: white;
    font-size: 20px;
    position: fixed;
    top: 80%;
    left: 85%;
}

.hidden {
    display: none !important;
}

input,
textarea {
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    width: 100%;
    font-family: 'Times New Roman', Times, serif;
}

input {
    height: 45px;
}

textarea {
    font-size: 20px;
}

.title {
    width: 75%;
    margin: 0 auto;
}

.template-main {
    font-family: 'pangolin';
}